.icon-left {
    position: absolute;
    top: 10px;
    left: 10px;
}

.icon-right {
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.season-display.winter i {
    color: blue;
}

.season-display.summer i {
    color: red;
}

.season-display {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
}

.winter {
    background-color: aliceblue;
}

.summer {
    background-color: orange;
}
.time {
    background-color: black;
    height: 1.5rem;
    color: white;
    text-align: center;
    font-size: 1rem;
    font-weight: bold;
}
